/*
*交付管理--编辑项目
*qiufh@bocspace.cn
*/
import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Breadcrumb, DatePicker, Row, Col, Form, Input, InputNumber, message, Radio, Button, Icon, Steps, Cascader, Select} from 'antd';
import {List} from 'immutable';
import MyUpload from "@/components/common/MyUpload";
import PersonAddIcon from "./PersonAddIcon2";
import EditableTableSubProject from "./EditableTableSubProject"
import {v4 as uuidv4} from 'uuid';
import url from 'url';
import cityData3 from "@/utils/cityData";
import _ from 'lodash';
import PageSelect from "@/components/common/PageSelect";

const {TextArea} = Input;
const { Step } = Steps;
const {Option} = Select;

@inject('DeliveryStore')
@withRouter
@Form.create()
@observer
export default class EditProject extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			visible: false,  // 下单成功
			status: "add",
			logoUrls: [],   // 企业logo
			tip: '正在生成请稍等...',
			isLoading: false,
			subProjects:[{address: "",
				id: "!"+uuidv4(),
				key: 0,
				masterName: "",
				name: "项目一",
				projectScale: ""},{address: "",
				id: "!"+uuidv4(),
				key: 0,
				masterName: "",
				name: "项目二",
				projectScale: ""}],
			selectedSubProjects:[],
			subProjectList:[],
			createStep:0,
			attachments:[],
			projectPurpose:"",
			projectTypes: []
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (nextProps.location.search!==this.props.location.search) {
			let search = nextProps.location.search;
			const status = url.parse(search, true).query.status;

			const {projectObj} = this.store;
			let fields = mobx.toJS(projectObj);

			this.setState({
				attachments:  fields.mainPhoto == null?[]:
					[{
					id: fields.id,
					uid: fields.id,
					name: fields.name,
					status: 'done',
					url: fields.mainPhoto,
						filePath: fields.mainPhoto,
						fileId: fields.id,
						fileName: fields.name
				}],
				status: status,
			});
		}
	}

	componentDidMount() {
		// 获取大文件上传sts
		this.store.getSts();
		const {projectObj} = this.store;
		let fields = mobx.toJS(projectObj);
		this.setState({
			attachments: fields.mainPhoto == null?[]:
				[{
			   id: fields.id,
			   uid: fields.id,
			   name: fields.name,
			   status: 'done',
			   url: fields.mainPhoto,
				filePath: fields.mainPhoto,
				fileId: fields.id,
				fileName: fields.name
			}]
		});
		this.store.getProjectTypeList().then((res)=>{
			if (res) {
				this.setState({
					projectTypes: res
				});
			}
		});
	}

	componentWillMount(){
		this.reSetForm();
	}

	handleAttment = (type, params) => {
		// 处理附件
		let arr = [];
		params.length && params.map((item) => {
			arr.push({
				fileId: item.fileId,
				fileName: item.fileName,
				filePath: item.filePath,
				id: item.id,
				uid: item.uid,
				name: item.fileName,
				status: 'done',
				url: item.filePath,
			})
		});
		this.setState({attachments: arr})
		const { projectObj } = this.store;
		this.store.projectObj = Object.assign(projectObj,{"mainPhoto":params[0]['filePath']})

	}


	handleBack = () => {
		this.props.history.push("/order?key=1");
	}

	transTxt = (fields) => {
		let logoName;
		if (fields.logoUrl!=null&&fields.logoUrl!="") {
			//获取最后一个.的位置
			var index= fields.logoUrl.lastIndexOf(".");
			//获取后缀
			var ext = fields.logoUrl.substr(index+1);
			logoName = "企业logo."+ext;
		}
		return logoName;
	}

	onClose = () => {
		this.setState({
			visible: false
		}, ()=>{
           this.handleBack();
		});
	}


	// 人员选择更新
	updatePerson = (userIds)=>{
		const { projectObj } = this.store;
		if(userIds.length>0){
			this.props.form.setFieldsValue({
				"masterName": userIds[0]['userName']
			});
			this.store.projectObj = Object.assign(mobx.toJS(projectObj),{"masterId":userIds[0]['userId'], "masterName":userIds[0]['userName'], "masters":userIds});
		}

	}


	saveContents=(dataSource)=>{
		this.setState({subProjects:dataSource})
	}

	setCreateStep=(step)=>{
		this.setState({createStep:step})
	}

	goToLink=(url)=>{
		this.props.history.push(url);
	}

	reSetForm=()=>{
		this.props.form.resetFields();

	}

   updateProject=()=>{
		const { projectObj } = this.store;
	   this.props.form.validateFields((err, values) => {
		   if(!err){
			 delete values.name;
		   	 const  newObj = Object.assign(
				 mobx.toJS(projectObj),
				 values,
				 {
					 "beginTime":values['beginTime']===''?null:values['beginTime'].valueOf(),
					 "endTime":values['endTime']===''?null:values['endTime'].valueOf(),
					// "tempBuildBeginTime":values['tempBuildBeginTime']===''?null:values['tempBuildBeginTime'].valueOf(),
					 //"tempBuildEndTime":values['tempBuildEndTime']===''?null:values['tempBuildEndTime'].valueOf(),
					 "masterName":projectObj.masterName,
					 "masterId":projectObj.masterId});
		   	 const{masters,...newProject} = newObj;
			   const parmas = {"p":Object.assign({},newProject)}
			   this.store.updateProject(parmas).then((res)=>{
					if(res){
						this.props.onCancel();
						this.store.getProjectPageList();
					}
			   })
		   }
	   })
   }

	// 高德地图地理编码将地址转化成经纬度
	onSearchAddress = (param) => {
		fetch('https://restapi.amap.com/v3/geocode/geo?key=08cde7c6ec084c2b876f82636820b573&address=' + param, {
			method: 'GET'
		})
			.then(res => {
				if (res.ok) {
					res.json().then(data => {
						let list = data.geocodes;
						let geocodes = list[0]['location'].split(',');
						this.store.projectObj = Object.assign(mobx.toJS(this.store.projectObj), {
							longitude: parseFloat(geocodes[0]),
							latitude: parseFloat(geocodes[1])
						});
					})
				}
			})
	}

	// 地区选择
	cityChange = async (type, value) => {
		this.store.projectObj[type] = Object.assign([], value);
		//console.log(value, value.join(""), '地区')
		if (value && value.length) {
			const param = value.join("");
			// 获取经纬度
			this.onSearchAddress(param);
		}
	}

	// 地区筛选
	filter = (inputValue, path) => {
		return path.some(option => option.label.toLowerCase().indexOf(inputValue.toLowerCase()) > -1);
	}

	convertStrToArr=(str)=>{
		let result = []
		let newStr = _.trim(str,'[');
		newStr = _.trim(newStr,']');
		result=  Array.isArray(newStr.split(","))?newStr.split(","):[]
		result = result.map((item)=>{
			return _.trim(item)
		})

		return result
	}


	handleChangeSelect=(value)=>{
		this.setState({projectPurpose:value})
	}

	// 产品类型
	changeBoxtype = (value) =>{
		const obj = {1: 19800, 2: 15600, 3: 13300};
		this.store.projectObj = Object.assign(mobx.toJS(this.store.projectObj), {
			"boxType": value,
			"boxMoney": obj[value]
		});
		this.props.form.setFieldsValue({
			"boxMoney": obj[value]
		});
	}

	updateStore = (param) =>{
		this.props.form.setFieldsValue({
			"companyId": param.id
		});
		this.store.projectObj = Object.assign(mobx.toJS(this.store.projectObj), {
			companyId: param.id,
			partyUnits: param.name
		});
	}

	handleChangeInput = (type, e) => {
		let val = e.target.value;
		this.store.projectObj = Object.assign(mobx.toJS(this.store.projectObj), {[type]: val});
	}

	handleChangeInput2 = (type, value) => {
		let val = value;
		this.store.projectObj = Object.assign(mobx.toJS(this.store.projectObj), {[type]: val});
	}


	render() {
		const {status,  attachments, projectPurpose, projectTypes} = this.state;
		const {ossData, projectObj} = this.store;

		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				md: {span: 8},
				sm: {span: 8},
			},
			wrapperCol: {
				xs: {span: 24},
				md: {span: 16},
				sm: {span: 16},
			},
		};
		let fields = mobx.toJS(projectObj);  // 基本信息
		const hidden = status=="view"?true:false;
		const boxTypeData = [{ name: '高档位', value: 1 }, { name: '中档位', value: 2 }, { name: '普通箱', value: 3 }];

		return (
			<div className="pb50">
				<div  className="mt30">
					<Form {...formItemLayout} className="ant-advanced-search-form">
						<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 12}, 15]}>
							<Col span={16}>
								<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 12}, 15]} style={{padding: 0, margin: "0"}}>
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="项目名称" className="w">
											{getFieldDecorator('name', {
												//initialValue: fields.name || "",
												rules: [{required: false, message: '此项是必填'}],
											})(
												<div>
													<Input value={fields.namePartyAShort} onChange={this.handleChangeInput.bind(this, "namePartyAShort")} placeholder="甲方简称" style={{width: "80px", padding: "4"}}/>
													<Input value={fields.namePlace} onChange={this.handleChangeInput.bind(this, "namePlace")} placeholder="项目地点" style={{width: "80px", margin: "0 2px", padding: "4"}}/>
													<Input value={fields.nameProjectShort} onChange={this.handleChangeInput.bind(this, "nameProjectShort")} placeholder="工程简称" style={{width: "80px", padding: "4"}}/>
													<Select value={fields.nameType==0?"":fields.nameType}
															onChange={this.handleChangeInput2.bind(this, "nameType")}
															placeholder={'类型'}
															style={{width: "80px", margin: "0 2px"}}>
														{
															projectTypes.map((v)=>{
																return <Option value={Number(v.id)} key={v.id}>{v.name}</Option>
															})
														}
													</Select>
													<span style={{fontSize: "12px"}}>项目</span>
												</div>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="项目经理" className="w detail_form_person" style={{width:'100%', height: '40px'}} >
											{getFieldDecorator('masterName', {
												initialValue: fields.masterName || "",
												rules: [{required: true, message: '此项是必填'}],
											})(
												<PersonAddIcon
													data={fields.masters==null?[]:fields.masters}
													updatePerson={this.updatePerson}
													disabled={hidden}
												/>
											)}
										</Form.Item>
									</Col>
									{/*<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="建筑面积（m2）" className="w">
											{getFieldDecorator('buildArea', {
												initialValue: fields.buildArea || "",
												rules: [{required: true, message: '此项是必填'}],
											})(
												<InputNumber placeholder="请输入" disabled={hidden} className="w"/>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="房间组成（间）" className="w">
											{getFieldDecorator('boxCount', {
												initialValue: fields.boxCount || "",
												rules: [{required: true, message: '此项是必填'}],
											})(
												<InputNumber placeholder="请输入" disabled={hidden} className="w"/>
											)}
										</Form.Item>
									</Col>*/}
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="项目地点" className="w">
											{getFieldDecorator('region', {
												initialValue: this.convertStrToArr(fields.region),
												rules: [{required: true, message: '此项是必填'}],
											})(
												<Cascader
													placeholder="省/市/区(县)"
													showSearch={this.filter}
													options={cityData3}
													value={fields.region == null ? [] : this.convertStrToArr(fields.region)}
													onChange={this.cityChange.bind(this, 'region')}
													expandTrigger="hover"
													key="region"
												/>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="详细地址" className="w">
											{getFieldDecorator('address', {
												initialValue: fields.address || "",
												rules: [{required: false, message: '此项是必填'}],
											})(
												<Input placeholder="请输入" disabled={hidden}/>
											)}
										</Form.Item>
									</Col>
									{/*<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="总造价(万元)" className="w">
											{getFieldDecorator('totalMoney', {
												initialValue: fields.totalMoney || "",
												rules: [{required: false, message: '此项是必填'}],
											})(
												<InputNumber placeholder="请输入" disabled={hidden} className="w"/>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="甲方单位" className="w">
											{getFieldDecorator('companyId', {
												initialValue: fields.companyId || "",
												rules: [{required: true, message: '此项是必填'}],
											})(
												<div>
													<PageSelect
														name={fields.partyUnits}
														value={fields.companyId || ""}
														updateStore={this.updateStore}
													/>
												</div>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="产品类型" className="w">
											{getFieldDecorator('boxType', {
												initialValue: fields.boxType || "",
												rules: [{required: true, message: '此项是必填'}],
											})(
												<Select onChange={this.changeBoxtype}>
													{boxTypeData.map((item, index) => {
														return <Option key={'boxTypeData' + index} value={item.value}>{item.name}</Option>;
													})}
												</Select>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label={'产品单价(元)'} className="w">
											{getFieldDecorator('boxMoney', {
												initialValue: fields.boxMoney || "",
												rules: [{required: false, message: '此项是必填'}],
											})(
												<InputNumber placeholder="请输入" className="w" disabled/>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label={'周转次数(次)'} className="w">
											{getFieldDecorator('turnoverCount', {
												initialValue: fields.turnoverCount,
												rules: [{required: true, message: '此项是必填'}],
											})(
												<InputNumber placeholder="请输入" className="w"/>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="临建开工日期" className="w">
											{getFieldDecorator('tempBuildBeginTime', {
												initialValue: fields.tempBuildBeginTime == null || fields.tempBuildBeginTime == 0 ? "" : moment(moment(fields.tempBuildBeginTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
												rules: [{required: true, message: '此项是必填'}],
											})(
												<DatePicker
													className="w"
													placeholder="请选择"
													format="YYYY-MM-DD"
													disabled={hidden}/>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="临建竣工日期" className="w">
											{getFieldDecorator('tempBuildEndTime', {
												initialValue: fields.tempBuildEndTime == null || fields.tempBuildEndTime == 0 ? "" : moment(moment(fields.tempBuildEndTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
												rules: [{required: true, message: '此项是必填'}],
											})(
												<DatePicker
													className="w"
													placeholder="请选择"
													format="YYYY-MM-DD"
													disabled={hidden}/>
											)}
										</Form.Item>
									</Col>*/}
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="工程开工日期" className="w">
											{getFieldDecorator('beginTime', {
												initialValue: fields.beginTime == null || fields.beginTime == 0 ? "" : moment(moment(fields.beginTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
												rules: [{required: false, message: '此项是必填'}],
											})(
												<DatePicker
													className="w"
													placeholder="请选择"
													format="YYYY-MM-DD"
													disabled={hidden}/>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
										<Form.Item label="工程竣工日期" className="w">
											{getFieldDecorator('endTime', {
												initialValue: fields.endTime == null || fields.endTime == 0 ? "" : moment(moment(fields.endTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
												rules: [{required: false, message: '此项是必填'}],
											})(
												<DatePicker
													className="w"
													placeholder="请选择"
													format="YYYY-MM-DD"
													disabled={hidden}/>
											)}
										</Form.Item>
									</Col>
								</Row>
							</Col>
							<Col span={8}>
								<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]} style={{padding: 0, margin: "0"}}>
									{/*<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
										<Form.Item label="项目用途" className="w">
											{getFieldDecorator('purpose', {
												initialValue:fields.purpose===null? undefined: this.convertStrToArr(fields.purpose),
												rules: [{required: true, message: '此项是必填'}],
											})(
												<Select mode="multiple"  placeholder={'请选择'}  onChange={this.handleChangeSelect}>
													<Option value="办公">办公</Option>
													<Option value="住宿">住宿</Option>
													<Option value="会议">会议</Option>
													<Option value="接待">接待</Option>
													<Option value="展示">展示</Option>
													<Option value="休闲">休闲</Option>
													<Option value="零售">零售</Option>
													<Option value="便民">便民</Option>
												</Select>
											)}
										</Form.Item>
									</Col>*/}
									<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
										<Form.Item
											label={<span>添加封面照片</span>}
											className="w">
											{getFieldDecorator('mainPhoto', {
												initialValue: fields.mainPhoto,
												rules: [{
													required: false,
													message: '此项是必填'}],
											})(
												<MyUpload
													key="mainPhoto"
													saveAttachment={this.handleAttment.bind(this, 'mainPhoto')}
													disabled={hidden}
													multiple={true}
													accept={".jpg,.jpeg,.gif,.png,.bmp"}
													listType="picture-card"
													ossData={mobx.toJS(ossData)}
													fileList={attachments}
													length={attachments.length}
												/>
											)}
										</Form.Item>
									</Col>
								</Row>
							</Col>
						</Row>
					</Form>
				</div>
				<div className="clearfix mt30" style={{display:'flex', justifyContent:'center'}}>
					<Button onClick={this.props.onCancel} type="default">取消</Button>
					<Button onClick={this.updateProject} type="primary" className="ml20">保存</Button>
				</div>
			</div>
		)
	}
}






