import React from 'react';
import {observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter, Link} from "react-router-dom";
import {
	DatePicker,
	Select,
	Button,
	Table,
	Pagination,
	Input,
	ConfigProvider,
	message,
	Icon,
	Modal,
	Radio,
	Popconfirm,
	Popover,
	Breadcrumb,
	Dropdown,
	Menu
} from 'antd';
import {downloadFile, download} from "@/utils/download";
import EditProject from "./EditProject";
import MyUpload from "./MyUploadNew2";

const {Search} = Input;
const customizeRenderEmpty = () => (
	<div style={{textAlign: 'center', padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png'/>
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}>暂无数据</p>
	</div>
);

@withRouter
@observer
class ProjectMgr extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.store;
		this.state = {
			visible: false,
			stateValue: 0,
			id: "",
			editVisible: false,
		}
	}

	componentDidMount() {

	}

	// 新增
	handleAdd = () => {
		this.props.history.push("/order/sales?status=add")
	}

	onChange = (type, e) => {
		this.store.salesParams[type] = e.target.value;
	}


	showSizeChange = async (current, pageSize) => {
		this.store.params.pageIndex = 1;
		this.store.params.pageSize = pageSize;
		this.getProjectPageList('');
	}

	onPageChange = async (pageNumber) => {
		this.store.params.pageIndex = pageNumber;
		this.getProjectPageList('');
	}

	showTotal = (total) => {
		return `共 ${total} 条`;
	}

	getProjectPageList = (text) => {
		this.store.getProjectPageList(text);
	}


	handleCancel = e => {
		this.setState({
			visible: false,
		});
	};


	confirm = (id) => {
		const params = {
			id: id
		}
		this.store.delSalesOrderState(params).then((res) => {
			if (res) {
				message.success("删除成功")
				this.store.getSalesOrderPageList();
			}
		})
	}

	cancel = (e) => {
	}


	// 手动下载
	downloadUrl = (record) => {
		const {filePath, fileName} = record;
		const isImage = (/\.(doc|docx)$/.test(fileName) || /\.(xls|xlsx)$/.test(fileName) || /\.(pdf)$/.test(fileName) || /\.(zip|rar)$/.test(fileName)) || (/\.(ppt|pptx)$/.test(fileName));
		if (isImage) {
			downloadFile(filePath, fileName);  // 直接下载pdf
		} else {
			this.downloadImage(filePath, fileName);
		}
	}

	// 直接下载
	downloadImage = (filePath, name) => {
		//获取最后一个.的位置
		var index = filePath.lastIndexOf(".");
		//获取后缀
		var ext = filePath.substr(index + 1);

		// 直接下载图片
		var x = new XMLHttpRequest();
		x.open("GET", filePath, true);
		x.responseType = 'blob';
		x.onload = function (e) {
			download(x.response, name, "image/" + ext);
		}
		x.send();
	}


	getStateInfo = (state) => {
		if (state === 0) {
			return "进行中"
		}
		if (state === 1) {
			return "已完成"
		}
	}

	delProject = (id) => {
		this.store.delProject({"id": id}).then((res) => {
			if (res) {
				message.success("删除成功");
				this.store.getProjectPageList('');
			} else {
				message.error("删除失败")
			}

		})
	}

	handleCloseEdit = () => {
		this.setState({editVisible: false})
	}

	showEditProjectModal = (id) => {
		this.store.getProjectById({"id": id}).then((res) => {
			if (res) {
				this.store.projectObj = Object.assign({}, res, {
					"masters": [{
						"userId": res.masterId,
						"userName": res.masterName
					}]
				});
				return true
			}

		}).then((res)=>{
			if(res){
				this.setState({editVisible: true})
			}
		})

	}

	handleSerach = (value) => {
		this.store.params.pageIndex = 1;
		this.getProjectPageList(value)
	}

	goToKeyLink = (url) => {
		this.props.history.push(url);
	}

	convertHttp2Https = (url) => {
		let fileUrl = url
		if (fileUrl.indexOf("http://") !== -1) {
			fileUrl = 'https://' + fileUrl.split('http://')[1];
		}
		fileUrl = decodeURIComponent(fileUrl.split('?')[0]);
		return fileUrl
	}

	// 附件
	handleAttment = (arr, record) => {

		const newObj = Object.assign(record, {"mainPhoto": this.convertHttp2Https(arr[0]['fileUrl'])})
		const {masters, keyIndex, ...p} = newObj;
		const params = {"p": p}
		this.store.updateProject(params).then((res) => {
			if (res) {
				this.getProjectPageList('');
			}
		})
	}

	handleProjectState = (key, record) => {
		const newObj = Object.assign(record, {"state": parseInt(key.key)})
		const {masters, keyIndex, ...p} = newObj;
		const params = {"p": p}
		this.store.updateProject(params).then((res) => {
			if (res) {
				this.getProjectPageList('');
			}
		})
	}

	projectStateMenu = (record) => {
		return (
			<Menu onClick={(key) => {
				this.handleProjectState(key, record)
			}}>
				<Menu.Item style={record['state'] === 0 ? {
					display: 'flex',
					justifyContent: 'space-between',
					alignItems: 'center'
				} : {display: 'flex', justifyContent: 'flex-end'}} key={0}>
					{record['state'] === 0 ? <Icon type="check"/> : null}<Button type="link">进行中</Button>
				</Menu.Item>
				<Menu.Item style={record['state'] === 1 ? {
					display: 'flex',
					justifyContent: 'space-between',
					alignItems: 'center'
				} : {display: 'flex', justifyContent: 'flex-end'}} key={1}>
					{record['state'] === 1 ? <Icon type="check"/> : null}<Button type="link">已完成</Button>
				</Menu.Item>
			</Menu>
		)
	}

	handleBack = () => {
		this.store.isProjectitem = false;
	}

	render() {
		const {projectList, projectTotalCount, params, projectsloading, ossData} = this.store;
		const {editVisible} = this.state;

		let columns = [
			{title: '序号', dataIndex: 'keyIndex', align: 'center', width: '50px'},
			{
				title: '状态', dataIndex: 'state', align: 'left', width: '80px', render: (text, record) => {
					return <span>{this.getStateInfo(text)}</span>;
				}
			},

			{
				title: '项目', className: 'vt', dataIndex: 'name', align: 'left', render: (text, record) => {
					return <div onClick={() => {
						this.goToKeyLink('/delivery/keylink?projectId=' + record['id'])
					}}
					            style={{
						            display: "flex",
						            justifyContent: "flex-start",
						            alignItems: "center",
						            cursor: "pointer"
					            }}
					>{<img width={120} height={63}
					       src={record.mainPhoto === null ? 'https://xinlj.oss-cn-beijing.aliyuncs.com/mainphoto/hrwxf.jpg' : record.mainPhoto}/>}
						<span style={{marginLeft: "10px"}}>{text}</span>
					</div>;
				}
			},
			{title: '项目编号', dataIndex: 'number', align: 'center', width: '100px'},
			{
				title: '创建日期', dataIndex: 'createTime', align: 'center', width: '100px', render: (text, record) => {
					return text === 0 ? "" : moment(text).format("YYYY-MM-DD")
				}
			},
			{title: '创建人', dataIndex: 'createUserName', align: 'center',},

			{
				title: '项目负责人', dataIndex: 'masterName', align: 'center', width: '100px',
				render: (text, record) => {
					return (<div style={{
						wordWrap: 'break-word',
						whiteSpace: 'pre-wrap'
					}}>{text}</div>)
				}
			},

			{
				title: '操作', dataIndex: 'caozuo', className: 'vt', align: 'center', render: (text, record) => {
					return (<div>
						<div style={{display: 'flex', justifyContent: 'center'}}>
							<Button onClick={() => {
								this.showEditProjectModal(record.id)
							}} type="link"> 编辑项目</Button><Popconfirm title="确定要删除吗?" onConfirm={() => {
							this.delProject(record.id)
						}}><Button type="link"> 删除项目</Button></Popconfirm>
						</div>
						<div style={{display: 'flex', justifyContent: 'center'}}>
							<MyUpload
								saveAttachment={(arr) => {
									this.handleAttment(arr, record)
								}}
								disabled={false}
								multiple={true}
								listType="text"
								ossData={mobx.toJS(ossData)}
								fileList={[]}
								journalType="journal"
							/>
							<Dropdown overlay={this.projectStateMenu(record)}>
								<Button className="pr" type="link">
									修改状态
									<Icon
										style={{position: "absolute", top: "10px", right: "-1px"}}
										type="down"/>
								</Button>
							</Dropdown>
						</div>
					</div>)
				}
			}
		];
		const {visible, stateValue} = this.state;
		const {pageIndex, pageSize} = mobx.toJS(params);
		return (
			<div style={{height: 'auto'}}>
				<div className="mt15">
					<Icon type="left" style={{color: "#57ACFF"}}/><Button style={{padding: "5px"}} type="link" onClick={this.handleBack}>返回项目首页</Button>
				</div>
				<div className="mt20" style={{display: 'flex', justifyContent: 'space-between'}}>
					<div>
						{/*<Button*/}
						{/*	className="fl"*/}
						{/*	type="dashed"*/}
						{/*	icon="edit"*/}
						{/*	onClick={this.handleAdd}*/}
						{/*>*/}
						{/*	修改状态*/}
						{/*</Button>*/}
					</div>
					<div>
						<Search
							placeholder="输入项目名称关键字"
							onSearch={value => {
								this.handleSerach(value)
							}}
							style={{width: 200}}
						/>
					</div>
				</div>
				<ConfigProvider renderEmpty={customizeRenderEmpty}>
					<Table
						className="m_table mt20"
						columns={columns}
						// dataSource={mobx.toJS(salesTable)}
						dataSource={mobx.toJS(projectList).map((item, index) => {
							return Object.assign(item, {'keyIndex': (pageIndex - 1) * (pageSize) + index + 1})
						})}
						size="middle"
						rowKey={record => record.id}
						pagination={false}
						loading={projectsloading}
					/>
					<Pagination
						className="mt20 tr"
						current={pageIndex}
						pageSize={pageSize}
						total={projectTotalCount}
						onShowSizeChange={this.showSizeChange}
						onChange={this.onPageChange}
						showSizeChanger
						showQuickJumper
						showTotal={this.showTotal}
					/>
				</ConfigProvider>
				<Modal
					centered={true}
					title={'编辑项目'}
					visible={editVisible}
					maskClosable={false}
					onCancel={this.handleCloseEdit}
					footer={null}
					className="task_detail_modal"
					style={{width: "70%", minWidth: "1000px"}}
					destroyOnClose={true}
				>
					<EditProject onCancel={this.handleCloseEdit}/>
				</Modal>
			</div>
		)
	}
}


export default ProjectMgr;



