/*
* 添加成员 单选
* dangw@glodon.com
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Icon, Input, Col, Row, Modal, Avatar,message, Button,Empty} from 'antd';
import { Scrollbars } from 'react-custom-scrollbars';
import * as _ from 'lodash'
import * as utils from '@/utils/utils';
const colorList = ['#f56a00', '#7265e6', '#ffbf00', '#00a2ae', '#F08080', '#EEE685', '#9400D3', '#D1EEEE', '#90EE90']; // 十个颜色

@inject('UserStore')
@observer
class PersonAddIcon extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            text: '',
            visible: false,
            oldSelect: this.props.data, // 已选择的人员,
            defaultData: [], // 默认数据
            data: [], // 已选择标记的数据
            filterData: [], // 筛选数据
        }
        this.getData = this.getData.bind(this);
        this.handleClickDebounce = _.debounce(this.getData, 500, { 'maxWait': 1000 });
    }

    componentWillReceiveProps(nextProps, nextContext) {
        if (JSON.stringify(nextProps.data)!=JSON.stringify(this.props.data)) {
            this.setState({
                oldSelect: nextProps.data
            },()=>{
                this.init();
            });
        }
    }

    componentDidMount() {
        const params = "";
        // 未选的全部用户
        this.props.UserStore.getRole(params).then((res) => {
            this.setState({
                defaultData: res.body.map((item)=>Object.assign(item, {selected: false}))
            },()=>{
                this.init();
            });
        });
    }

    // 初始化
    init = () => {
        let defaultData = JSON.parse(JSON.stringify(this.state.defaultData));
        //  将已选定的用户做标记
        let arr = defaultData.map((item) => {
            let obj = {}
            if ([...this.state.oldSelect].some(v => v.userId === item.id)) {
                obj = Object.assign(item, {selected: true})
            } else {
                obj = Object.assign(item, {selected: false})
            }
            return obj;
        });
        this.setState({
            data: Object.assign([], arr)
        });
    }

    componentWillUnmount() {
        this.handleClickDebounce.cancel();
    }

    // 提交
    handleOk = () => {
        let dataArray = JSON.parse(JSON.stringify(this.state.data));
        const users = dataArray.filter((res)=>{return res.selected==true}).map((item, index) => {
            return {userId: item.id, userName: item.realname}
        })
        this.handleCancel();
        this.props.updatePerson(users);
    };

    // 取消
    handleCancel = () => {
        this.setState({
            visible: false,
            text: "",
            data: Object.assign([], this.state.defaultData)
        })
    };

    // 点击列表选中
    handClick = (param) => {
        let dataArray = JSON.parse(JSON.stringify(this.state.data));

        const dindex = dataArray.map((item)=>Object.assign(item, {selected: false})).findIndex((item) => {
            return item.id == param.id
        })
        dataArray.splice(dindex, 1, Object.assign(param, {selected: true}))
        this.setState({
            data: Object.assign([], dataArray)
        })
    }

    // 点击列表删除
    handClickDel = (param) => {
        let dataArray = JSON.parse(JSON.stringify(this.state.data));
        const dindex = dataArray.findIndex((item) => {
            return item.id == param.id
        })
        dataArray.splice(dindex, 1, Object.assign(param, {selected: false}))
        this.setState({
            data: Object.assign([], dataArray)
        })
    }

    // 模糊搜索
    onChange = (e) => {
        this.setState({
            text: _.trim(e.target.value)
        });
        this.handleClickDebounce()
    }

    getData(){
        const {text, data} = this.state;
        let dataArray = JSON.parse(JSON.stringify(data));
        let filterData = dataArray.filter(item => (item.realname).indexOf(text) > -1 || (item.phone).indexOf(text) > -1)
        this.setState({
            filterData: filterData
        });
    }

    // 新增
    handleAdd = () => {
        this.setState({
            visible: true,
            text: ""
        });
    }

    // 删除
    handleClose = (record) => {
        const dataSource = [...this.state.oldSelect];
        this.setState({
            oldSelect: dataSource.filter(item => item.userId !== record.userId)
        }, ()=>{
           this.props.updatePerson(this.state.oldSelect);
        });
    }

    render() {
       const  {visible, data, oldSelect, filterData, text} = this.state;
       const {disabled} = this.props;
       let isFilter = text==""||text==null;
       const activeData = isFilter?data:filterData;  // 当前数据
        return (
            <div style={{position:'relative',
                height: '34px',
                lineHeight: '34px',
                borderRadius: '4px',
                width: '100%',
                border: '1px solid #d9d9d9',
                padding: '0px 3px'}}>
                {oldSelect.length>0?oldSelect.map((item, index)=>{
                    return <span className="">
                                 <span
                                     style={{padding:disabled?"0 5px":"0 15px  0 5px"}}
                                     className="person_add">
                                     {item.userName}
                                     <Icon
                                         onClick={this.handleClose.bind(this, item)}
                                         type="close"
                                         className={disabled?"hidden":"person_close"}/>
                                 </span>
                               </span>
                }):<span style={{color: '#bfbfbf', position:'absolute', top: '0px', left: '10px'}}>请选择</span>}
                <Button
                        shape="circle"
                        style={{
                            width:'28px',
                            minWidth: '28px',
                            height:'28px',
                            position:'absolute', top: '2px', right: '2px',
                            fontSize: '18px'}}
                        onClick={this.handleAdd}
                        disabled={disabled}
                        icon="usergroup-add"/>
                <Modal
                    maskClosable={false}
                    closable={false}
                    okText={'提交'}
                    title="添加负责人"
                    visible={visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <Row type="flex" justify="space-between" className="">
                        <Col span={12} className='pr20'>
                            <Input
                                placeholder="输入姓名/手机号"
                                suffix={<Icon type="search" />}
                                onChange={this.onChange}
                            />
                            <Scrollbars
                                autoHide
                                autoHideTimeout={1000}
                                autoHideDuration={200}
                                autoHeight
                                autoHeightMin={0}
                                autoHeightMax={350}
                                thumbMinSize={30}
                                universal={true}
                            >
                                <ul className="userlist_ul">
                                    {activeData.length > 0 && activeData.map((item, index) => {
                                        return <li
                                            key={index}
                                            className="pr"
                                            onClick={this.handClick.bind(this, item)}>
                                            <div className="ul_name">{item.realname}</div>
                                            <div className="ul_station">{item.station}</div>
                                            <div className="ul_email">{item.email}</div>
                                            <Icon
                                                style={{
                                                    position: 'absolute',
                                                    top: '10px',
                                                    right: '15px',
                                                    fontSize: '16px',
                                                    color: 'rgba(75,164,190,1)'
                                                }}
                                                className={item.selected?"active":"hidden"} type="check" />
                                        </li>
                                    })
                                    }
                                </ul>
                            </Scrollbars>
                            {activeData.length==0?
                                <Empty
                                    image="https://gw.alipayobjects.com/mdn/miniapp_social/afts/img/A*pevERLJC9v0AAAAAAAAAAABjAQAAAQ/original"
                                    imageStyle={{
                                        height: 60,
                                        marginTop:"30px"
                                    }}
                                    description={
                                        "暂无匹配"
                                    }
                                >
                                </Empty>
                                :null}
                        </Col>
                        <Col span={12} className="tl" style={{
                            minHeight: '350px',
                            borderLeft: '1px solid #e6e6e6'
                        }}>
                            <p className="pl20">已选成员({data&&data.filter((res)=>{return res.selected==true}).length})</p>
                            <ul className="userlist_ul_del">
                                {data.filter((res)=>{return res.selected==true}).length>0&&data.filter((res)=>{return res.selected==true}).map((item, index) => {
                                    return <li
                                        key={index}
                                        className="pr"
                                        onClick={this.handClickDel.bind(this, item)}>
                                        <Avatar
                                            style={{ display:'inlineBlock',backgroundColor: colorList[index%10], verticalAlign: 'middle' }} size="small">
                                            {item.realname.slice(0,1)}
                                        </Avatar>  {/*颜色随机取值*/}
                                        <span className="ul_name">{item.realname}</span>
                                        <Icon
                                            style={{
                                                position: 'absolute',
                                                top: '13px',
                                                right: '15px',
                                                fontSize: '16px',
                                                color: 'rgba(184,80,74,1)',
                                                cursor: 'pointer'
                                            }}
                                            className="userlist_ul_icon"
                                            type="close" />
                                    </li>
                                })
                                }
                            </ul>
                        </Col>
                    </Row>
                </Modal>
            </div>
        )
    }
}


export default PersonAddIcon;



