import React from 'react';
import {inject, observer} from 'mobx-react';
import {withRouter} from "react-router-dom";
import {Tabs} from 'antd';
import ProjectMgr from './ProjectMgr';
import url from 'url';
const {TabPane} = Tabs;

@inject('DeliveryStore')
@withRouter
@observer
class DeliveryProList extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			activeKey: "1",  // 当前活动key
		}
	}

	componentDidMount() {
		let search = this.props.location.search;
		const key = url.parse(search, true).query.key;
		this.loadPageInfo();
		// // 获取大文件上传sts
		// this.store.getSts();
		// // 已有权限
		// this.store.getUserPermissionByAppCode();
	}

	loadPageInfo = () =>{
		this.store.getProjectPageList('');
	}

	callback = (key) => {
		this.props.history.push("/order?key="+key);
		this.setState({
			activeKey: key
		}, () => {
			if (key == 1) {  // 销售
				this.store.getSalesOrderPageList();
			} else {  // 采购
				this.store.getPurchasingOrderPageList();
			}
		});
	}

	render() {
		const {activeKey} = this.state;
		return (
			<div>
				<ProjectMgr
					store={this.store}
				/>
			</div>
		)
	}
}


export default DeliveryProList;



